html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  padding: 0;
  background-color: #ffffff;
  color: #000000;
  font-family: "PT sans", sans-serif;
  line-height: 1.3;
  margin: 0;
}

.main {
  padding: 20px;
  max-width: 1040px;
  width: 100%;
  margin: 0 auto;
}

@media only screen and (min-width: 1000px) {
  .content {
    flex: 3;
  }
}

@media only screen and (min-width: 1000px) {
  aside {
    flex: 1;
  }
}

.button {
  padding: 12 18;
  font-size: 16px;
  display: block;
  width: fit-content;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 600;
  transition: all 400ms ease-in-out;
}
.button--yellow {
  border: 2px solid #FCDC17;
  color: #ffffff;
}
.button--yellow:hover {
  background-color: #FCDC17;
  color: black;
}

h1, h2, h3 {
  font-weight: 700;
  margin-top: 0px;
}

h1 {
  font-size: 2rem;
}
@media only screen and (min-width: 1000px) {
  h1 {
    font-size: 3rem;
  }
}

h2 {
  font-size: 1.5rem;
}
@media only screen and (min-width: 1000px) {
  h2 {
    font-size: 2rem;
  }
}

h3 {
  font-size: 1.25rem;
}
@media only screen and (min-width: 1000px) {
  h3 {
    font-size: 1.5rem;
  }
}

.site__logo {
  width: 100%;
  padding: 1rem;
}
.site__logo img {
  width: 125px;
  margin: auto;
  display: block;
}

.site__nav {
  width: 100%;
  border-top: 1px solid #FCDC17;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1rem;
}
.site__nav .navSocials {
  width: 20%;
}
.site__nav .navSocials ul {
  list-style: none;
}
.site__nav .navSocials ul li {
  display: inline-block;
}
.site__nav .navSocials ul li:not(:first-child) {
  margin: 0 10px;
}
.site__nav .navSocials .navSocials__icon {
  width: 20px;
}
.site__nav .navigation ul {
  list-style: none;
  padding: 0;
}
.site__nav .navigation ul li {
  display: inline-block;
  color: #ffffff;
  margin: 0 10px;
  transition: 200ms ease-in-out;
  cursor: pointer;
}
.site__nav .navigation ul li:hover {
  color: #FCDC17;
}
.site__nav .navButton {
  width: 20%;
  text-align: end;
}
.site__nav .navButton .button {
  margin-left: auto;
}

.hero__background {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  background-image: url(../app/images/content/backgrounds/homepage_hero.jpg);
  margin: 0;
  padding: 0;
  height: 1000px;
  background-position: center;
}
.hero__background::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background: linear-gradient(180deg, #111 23%, hsla(0deg, 0%, 7%, 0) 52%);
  width: 100%;
  height: 100%;
  opacity: 0.65;
}
.hero__background::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 70px;
  background: url(./../app/images/content/backgrounds/hero_paperbottom.png);
  background-repeat: 0;
  background-position: bottom;
}

.hero {
  width: 100%;
  height: 700px;
  position: relative;
}
.hero .hero__inner {
  width: 100%;
  max-width: 500px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: #ffffff;
}
.hero .hero__inner small {
  color: #FCDC17;
}
.hero .hero__inner h1 {
  font-size: 52px;
  font-weight: 700;
}
.hero .hero__inner .button {
  margin: auto;
}

.section--about {
  height: 600px;
  padding: 60px 0;
}